import React from 'react'
import {withRouter} from "react-router-dom";
import {connect} from "react-redux";
import appActions from "../../actions/appActions";

/**
 * CookiesBanner component
 *
 * @returns {*}
 * @constructor
 */
const CookiesBanner = (props) => {
    return (
        <div className='cookies-banner'>
            <p>
                      
                 cookies,           .
                     ,       
                  <a href="http://www.invischat.com/privacy_policy"> </a>
            </p>
            <button className="button ok-button" onClick={props.acceptCookies}></button>
        </div>
    )
};

const mapStateToProps = () => {
    return {}
};

const mapDispatchToProps = (dispatch) => {
    return {
        /**
         * Accept Cookies
         */
        acceptCookies: () => {
            dispatch(appActions.acceptCookies())
        }
    }
};

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(CookiesBanner))
